<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="bonotyg.meta_semana"
    :progress="bonotyg.progress"
    :name="bonotyg.nombre"
    :depto="bonotyg.depto"
    :days="bonotyg.dias_laborados"
    :m3="bonotyg.m3_persona"
    :bonus_p="bonotyg.bono_persona"
    :bonus_tot="bonotyg.bono_depto"
    :asis="bonotyg.asistencia"
    :extra="bonotyg.$_extra_m3"
    :bonus_prod="bonotyg.bono_productividad"
    :bonus_goals="bonotyg.bono_metas"
    />
</div>
</template>

<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Bonotyg',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'bonotyg',
            userCode: this.$route.params.user,
            city: 'tijuana'
        }); 
    },
    computed: {
        isLoading() {            
            return this.$store.getters["plants/loading"];
        },
        bonotyg() {            
            return this.$store.getters["plants/data"];
        }
    }
}
</script>